<template>
  <div class="wl-transfer transfer" :style="{ width, height }">
    <template v-if="mode == 'transfer'">
      <!--   -->
      <div class="transfer-left">
        <h3 class="transfer-title">
          <el-checkbox
            :indeterminate="from_is_indeterminate"
            v-model="from_check_all"
            @change="fromAllBoxChange"
          ></el-checkbox>
          <span>{{ fromTitle }}</span>
          <slot name="title-left"></slot>
        </h3>
        <!--  -->
        <div class="transfer-main">
          <slot name="from"></slot>
          <el-input
            v-if="filter"
            :placeholder="placeholder"
            v-model="filterFrom"
            size="small"
            class="filter-tree"
          ></el-input>
          <el-tree
            ref="from-tree"
            show-checkbox
            :lazy="lazy"
            :node-key="node_key"
            :load="leftloadNode"
            :props="defaultProps"
            :data="self_from_data"
            :accordion="accordion"
            :default-expand-all="openAll"
            :highlight-current="highLight"
            :check-strictly="checkStrictly"
            :render-content="renderContentLeft"
            :filter-node-method="filterNodeFrom"
            :default-checked-keys="defaultCheckedKeys"
            :default-expanded-keys="from_expanded_keys"
            @check="fromTreeChecked"
          ></el-tree>
          <slot name="left-footer"></slot>
        </div>
      </div>
      <!--   -->
      <div class="transfer-center">
        <template v-if="button_text">
          <p class="transfer-center-item">
            <el-button
              type="primary"
              @click="addToAims(true)"
              :disabled="from_disabled"
            >
              {{ fromButton || "" }}
              <i class="el-icon-arrow-right"></i>
            </el-button>
          </p>
          <p class="transfer-center-item">
            <el-button
              type="primary"
              @click="removeToSource"
              :disabled="to_disabled"
              icon="el-icon-arrow-left"
              >{{ toButton || "" }}</el-button
            >
          </p>
        </template>
        <template v-else>
          <p class="transfer-center-item">
            <el-button
              type="primary"
              @click="addToAims(true)"
              icon="el-icon-arrow-right"
              circle
              :disabled="from_disabled"
            ></el-button>
          </p>
          <p class="transfer-center-item">
            <el-button
              type="primary"
              @click="removeToSource"
              :disabled="to_disabled"
              icon="el-icon-arrow-left"
              circle
            ></el-button>
          </p>
        </template>
      </div>
      <!--   -->
      <div class="transfer-right">
        <h3 class="transfer-title">
          <el-checkbox
            :indeterminate="to_is_indeterminate"
            v-model="to_check_all"
            @change="toAllBoxChange"
          ></el-checkbox>
          <span>{{ toTitle }}</span>
          <slot name="title-right"></slot>
        </h3>
        <!--  -->
        <div class="transfer-main">
          <slot name="to"></slot>
          <el-input
            v-if="filter"
            :placeholder="placeholder"
            v-model="filterTo"
            size="small"
            class="filter-tree"
          ></el-input>
          <el-tree
            slot="to"
            ref="to-tree"
            show-checkbox
            :lazy="lazyRight"
            :data="self_to_data"
            :node-key="node_key"
            :props="defaultProps"
            :load="rightloadNode"
            :default-expand-all="openAll"
            :highlight-current="highLight"
            :check-strictly="checkStrictly"
            :render-content="renderContentRight"
            :filter-node-method="filterNodeTo"
            :default-expanded-keys="to_expanded_keys"
            @check="toTreeChecked"
          ></el-tree>
          <slot name="right-footer"></slot>
        </div>
      </div>
    </template>
    <template v-else-if="mode == 'addressList'">
      <!--   -->
      <div class="transfer-left">
        <h3 class="transfer-title">
          <el-checkbox
            :indeterminate="from_is_indeterminate"
            v-model="from_check_all"
            @change="fromAllBoxChange"
          ></el-checkbox>
          <span>{{ fromTitle }}</span>
        </h3>
        <!--  -->
        <div class="transfer-main">
          <slot name="from"></slot>
          <el-input
            v-if="filter"
            :placeholder="placeholder"
            v-model="filterFrom"
            size="small"
            class="filter-tree"
          ></el-input>
          <el-tree
            ref="from-tree"
            show-checkbox
            :node-key="node_key"
            :props="defaultProps"
            :data="self_from_data"
            :default-expand-all="openAll"
            :highlight-current="highLight"
            :render-content="renderContentLeft"
            :filter-node-method="filterNodeFrom"
            :default-expanded-keys="from_expanded_keys"
            @check="fromTreeChecked"
          ></el-tree>
        </div>
      </div>
      <!--   -->
      <div class="transfer-center address-list-center">
        <p
          class="transfer-center-item"
          v-show="!move_up"
          :class="{ 'address-only-item': addressOptions.num === 1 }"
        >
          <el-button
            type="primary"
            @click="addressListTransfer(0)"
            icon="el-icon-arrow-right"
            circle
            class="address-first-btn"
            :disabled="from_disabled"
          ></el-button>
        </p>
        <p class="transfer-center-item" v-if="addressOptions.num > 1">
          <el-button
            type="primary"
            @click="addressListTransfer(1)"
            :disabled="from_disabled"
            icon="el-icon-arrow-right"
            circle
          ></el-button>
        </p>
        <p class="transfer-center-item" v-show="move_up">
          <el-button
            type="primary"
            @click="addressListTransfer(2)"
            :disabled="from_disabled"
            icon="el-icon-arrow-right"
            circle
          ></el-button>
        </p>
      </div>
      <div class="transfer-right">
        <div
          class="transfer-right-item"
          :class="{
            'transfer-right-small': move_up,
            'transfer-right-only': addressOptions.num === 1,
          }"
        >
          <h3 class="transfer-title">
            <span>{{ toTitle }}</span>
            <span class="u-clear" @click="clearList(0, 'all')" v-if="!move_up"
              ></span
            >
            <img
              class="move_up_img move_down_img"
              v-else
              src="./shang.png"
              alt
              @click="moveUp('down')"
            />
          </h3>
          <!--  -->
          <div class="transfer-main" v-if="!move_up">
            <slot name="to"></slot>
            <el-input
              v-if="filter"
              :placeholder="placeholder"
              v-model="filterListFirst"
              size="small"
              class="filter-tree"
            ></el-input>
            <ul class="address-list-ul">
              <li
                class="address-list-li"
                v-for="item of addressee"
                :key="item[node_key]"
              >
                <label>
                  {{ item[defaultProps.label] }}
                  {{ addressOptions.connector }}
                  {{ item[addressOptions.suffix] }}
                </label>
                <i
                  class="address-list-del el-icon-delete"
                  @click="clearList(0, item[node_key])"
                ></i>
              </li>
            </ul>
          </div>
        </div>
        <div class="transfer-right-item" v-if="addressOptions.num >= 2">
          <h3 class="transfer-title">
            <span>{{ toTitleSecond || "" }}</span>
            <span class="u-clear" @click="clearList(1, 'all')"></span>
          </h3>
          <!--  -->
          <div class="transfer-main">
            <slot name="to"></slot>
            <el-input
              v-if="filter"
              :placeholder="placeholder"
              v-model="filterListSecond"
              size="small"
              class="filter-tree"
            ></el-input>
            <ul class="address-list-ul">
              <li
                class="address-list-li"
                v-for="item of Cc"
                :key="item[node_key]"
              >
                <label>
                  {{ item[defaultProps.label] }}
                  {{ addressOptions.connector }}
                  {{ item[addressOptions.suffix] }}
                </label>
                <i
                  class="address-list-del el-icon-delete"
                  @click="clearList(1, item[node_key])"
                ></i>
              </li>
            </ul>
          </div>
        </div>
        <div
          v-if="addressOptions.num === 3"
          class="transfer-right-item"
          :class="{ 'transfer-right-small': !move_up }"
        >
          <h3 class="transfer-title">
            <span>{{ toTitleThird || "" }}</span>
            <span class="u-clear" @click="clearList(2, 'all')" v-if="move_up"
              ></span
            >
            <img
              class="move_up_img"
              v-else
              src="./shang.png"
              alt
              @click="moveUp('up')"
            />
          </h3>
          <!--  -->
          <div class="transfer-main" v-if="move_up">
            <slot name="to"></slot>
            <el-input
              v-if="filter"
              :placeholder="placeholder"
              v-model="filterListThird"
              size="small"
              class="filter-tree"
            ></el-input>
            <ul class="address-list-ul">
              <li
                class="address-list-li"
                v-for="item of secret_receiver"
                :key="item[node_key]"
              >
                <label>
                  {{ item[defaultProps.label] }}
                  {{ addressOptions.connector }}
                  {{ item[addressOptions.suffix] }}
                </label>
                <i
                  class="address-list-del el-icon-delete"
                  @click="clearList(2, item[node_key])"
                ></i>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import { arrayToTree, valInDeep } from "wl-core";

export default {
  name: "wl-tree-transfer",
  data() {
    return {
      from_is_indeterminate: false, // 
      from_check_all: false, // 
      to_is_indeterminate: false, // 
      to_check_all: false, // 
      from_expanded_keys: [], // 
      to_expanded_keys: [], // 
      from_disabled: true, // 
      to_disabled: true, // 
      from_check_keys: [], // key 
      to_check_keys: [], // key 
      filterFrom: "", // 
      filterTo: "", // 
      filterListFirst: "", //  1
      filterListSecond: "", //  2
      filterListThird: "", //  3
      archiveFirst: [], // 
      archiveSecond: [], // 
      archiveThird: [], // 
      addressee: [], // 
      Cc: [], // 
      secret_receiver: [], // 
      move_up: false, //  
    };
  },
   props: {
    sjr: {
      type: Array,
      default: () => {
        return [];
      },
    },
    csr: {
      type: Array,
      default: () => {
        return [];
      },
    },
    msr: {
      type: Array,
      default: () => {
        return [];
      },
    },
    // 
    width: {
      type: String,
      default: "100%",
    },
    // 
    height: {
      type: String,
      default: "320px",
    },
    // 
    title: {
      type: Array,
      default: () => ["", ""],
    },
    // 
    button_text: Array,
    // 
    from_data: {
      type: Array,
      default: () => [],
    },
    // 
    to_data: {
      type: Array,
      default: () => [],
    },
    // el-tree 
    defaultProps: {
      type: Object,
      default: () => {
        return { label: "label", children: "children" };
      },
    },
    // el-tree node-key 
    node_key: {
      type: String,
      default: "id",
    },
    //  pid
    pid: {
      type: String,
      default: "pid",
    },
    // pid
    rootPidValue: {
      type: [String, Number],
      default: 0,
    },
    // 
    filter: {
      type: Boolean,
      default: false,
    },
    // 
    openAll: {
      type: Boolean,
      default: false,
    },
    // 
    renderContentLeft: Function,
    // 
    renderContentRight: Function,
    // 
    mode: {
      type: String,
      default: "transfer",
    },
    //  num->  suffix-> labelididconnector -> 
    addressOptions: {
      type: Object,
      default: () => {
        return {
          num: 3,
          suffix: "suffix",
          connector: "-",
        };
      },
    },
    // 
    transferOpenNode: {
      type: Boolean,
      default: true,
    },
    //  
    defaultCheckedKeys: {
      type: Array,
      default: () => [],
    },
    //  
    defaultExpandedKeys: {
      type: Array,
      default: () => [],
    },
    // placeholder
    placeholder: {
      type: String,
      default: "",
    },
    // 
    filterNode: Function,
    // 
    defaultTransfer: {
      type: Boolean,
      default: false,
    },
    // arrayToTree
    arrayToTree: {
      type: Boolean,
      default: false,
    },
    // 
    lazy: {
      type: Boolean,
      default: false,
    },
    // 
    lazyRight: {
      type: Boolean,
      default: false,
    },
    // 
    lazyFn: Function,
    //  false
    highLight: {
      type: Boolean,
      default: false,
    },
    // 
    checkStrictly: {
      type: Boolean,
      default: false,
    },
    // 
    accordion: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    // ----------------------------------------------------
    // 
    addToAims(emit) {
      // keys - id
      let keys = this.$refs["from-tree"].getCheckedKeys();
      // keys - id
      let harfKeys = this.$refs["from-tree"].getHalfCheckedKeys();
      // 
      let arrayCheckedNodes = this.$refs["from-tree"].getCheckedNodes();
      // nodes - 
      let nodes = JSON.parse(JSON.stringify(arrayCheckedNodes));
      // 
      let arrayHalfCheckedNodes = this.$refs["from-tree"].getHalfCheckedNodes();
      // nodes - 
      let halfNodes = JSON.parse(JSON.stringify(arrayHalfCheckedNodes));
      // 
      let children__ = this.defaultProps.children || "children";
      let pid__ = this.pid || "pid";
      let id__ = this["node_key"] || "id";
      let root__ = this.rootPidValue || 0;
      // let self__keys__ = []; // 
      // 
      if (this.checkStrictly) {
        this.checkStrictlyTransfer(
          arrayCheckedNodes,
          { children__, pid__, id__, root__ },
          true
        );
      } else {
        /*
         * 
         *  - 
         * pid
         *  -   
         * 
         */

        // let  
        // let self_to_data = JSON.stringify(this.self_to_data);
        // 
        let skeletonHalfCheckedNodes = JSON.parse(JSON.stringify(arrayHalfCheckedNodes)); //  - 
        //  - 
        let newSkeletonHalfCheckedNodes = [];
        skeletonHalfCheckedNodes.forEach((item) => {
          // 
          let inThere = valInDeep(this.self_to_data, item[id__], id__, children__);
          if (!inThere.length) {
            newSkeletonHalfCheckedNodes.push(item);
          }
        });
        // - - 
        newSkeletonHalfCheckedNodes.forEach((item) => {
          item[children__] = [];
          root__ !== item[pid__]
            ? this.$refs["to-tree"].append(item, item[pid__])
            : this.$refs["to-tree"].append(item);
        });

        // 
        //  - 
        let newSkeletonCheckedNodes = [];
        nodes.forEach((item) => {
          // 
          let inThere = valInDeep(this.self_to_data, item[id__], id__, children__);
          if (!inThere.length) {
            newSkeletonCheckedNodes.push(item);
          }
        });
        // - - 
        newSkeletonCheckedNodes.forEach((item) => {
          if (item[children__] && item[children__].length > 0) {
            item[children__] = [];
            root__ !== item[pid__]
              ? this.$refs["to-tree"].append(item, item[pid__])
              : this.$refs["to-tree"].append(item);
          }
        });

        //   - 
        let leafCheckedNodes = arrayCheckedNodes.filter(
          (item) => !item[children__] || item[children__].length == 0
        );
        // 
        leafCheckedNodes.forEach((item) => {
          // 
          let inThere = valInDeep(this.self_to_data, item[id__], id__, children__);
          if (!inThere.length) {
            this.$refs["to-tree"].append(item, item[pid__]);
          }
        });

        // 
        arrayCheckedNodes.map((item) => this.$refs["from-tree"].remove(item));
      }

      // 
      this.from_check_keys = [];

      // 
      if (this.transferOpenNode && !this.lazy) {
        this.to_expanded_keys = keys;
      }

      // 
      emit &&
        this.$emit("addBtn", this.self_from_data, this.self_to_data, {
          keys,
          nodes,
          harfKeys,
          halfNodes,
        });

      // 
      this.$refs["from-tree"].setCheckedKeys([]);
    },
    // 
    removeToSource() {
      // keys - id
      let keys = this.$refs["to-tree"].getCheckedKeys();
      // keys - id
      let harfKeys = this.$refs["to-tree"].getHalfCheckedKeys();
      // nodes 
      let arrayCheckedNodes = this.$refs["to-tree"].getCheckedNodes();
      // nodes - 
      let nodes = JSON.parse(JSON.stringify(arrayCheckedNodes));
      // 
      let arrayHalfCheckedNodes = this.$refs["to-tree"].getHalfCheckedNodes();
      // nodes - 
      let halfNodes = JSON.parse(JSON.stringify(arrayHalfCheckedNodes));
      // 
      let children__ = this.defaultProps.children || "children";
      let pid__ = this.pid || "pid";
      let id__ = this["node_key"] || "id";
      let root__ = this.rootPidValue || 0;
      // 
      if (this.checkStrictly) {
        this.checkStrictlyTransfer(
          arrayCheckedNodes,
          { children__, pid__, id__, root__ },
          false
        );
      } else {
        /*
         *  - 
         * pid
         *  -   
         * 
         */

        // let  
        // let self_from_data = JSON.stringify(this.self_from_data);
        // 
        let skeletonHalfCheckedNodes = JSON.parse(JSON.stringify(arrayHalfCheckedNodes)); //  - 
        //  - 
        let newSkeletonHalfCheckedNodes = [];
        skeletonHalfCheckedNodes.forEach((item) => {
          // 
          let inThere = valInDeep(this.self_from_data, item[id__], id__, children__);
          if (!inThere.length) {
            newSkeletonHalfCheckedNodes.push(item);
          }
        });
        // - - 
        newSkeletonHalfCheckedNodes.forEach((item) => {
          item[children__] = [];
          root__ !== item[pid__]
            ? this.$refs["from-tree"].append(item, item[pid__])
            : this.$refs["from-tree"].append(item);
        });

        // 
        //  - 
        let newSkeletonCheckedNodes = [];
        nodes.forEach((item) => {
          // 
          let inThere = valInDeep(this.self_from_data, item[id__], id__, children__);
          if (!inThere.length) {
            newSkeletonCheckedNodes.push(item);
          }
        });
        // - - 
        newSkeletonCheckedNodes.forEach((item) => {
          if (item[children__] && item[children__].length > 0) {
            item[children__] = [];
            root__ !== item[pid__]
              ? this.$refs["from-tree"].append(item, item[pid__])
              : this.$refs["from-tree"].append(item);
          }
        });

        //   - 
        let leafCheckedNodes = arrayCheckedNodes.filter(
          (item) => !item[children__] || item[children__].length == 0
        );
        // 
        leafCheckedNodes.forEach((item) => {
          // 
          let inThere = valInDeep(this.self_from_data, item[id__], id__, children__);
          if (!inThere.length) {
            this.$refs["from-tree"].append(item, item[pid__]);
          }
        });

        // 
        arrayCheckedNodes.map((item) => this.$refs["to-tree"].remove(item));
      }

      // 
      this.to_check_keys = [];

      // 
      if (this.transferOpenNode && !this.lazy) {
        this.from_expanded_keys = keys;
      }

      // 
      this.$emit("removeBtn", this.self_from_data, this.self_to_data, {
        keys,
        nodes,
        harfKeys,
        halfNodes,
      });
      // 
      this.$refs["to-tree"].setCheckedKeys([]);
    },
    /**
     * @name 
     * @param {Array} nodes 
     * @param {Object} options { children__, pid__, id__, root__ }
     * @param {Boolean} isAdd addremove
     */
    checkStrictlyTransfer(nodes, options, isAdd) {
      // |
      let from_data = [];
      let to_data = [];
      let from_ref = "";
      let to_ref = "";
      if (isAdd) {
        from_data = this.self_from_data;
        to_data = this.self_to_data;
        from_ref = "from-tree";
        to_ref = "to-tree";
      } else {
        from_data = this.self_to_data;
        to_data = this.self_from_data;
        from_ref = "to-tree";
        to_ref = "from-tree";
      }
      // 
      const new_nodes = nodes.map((i) => {
        let new_node = Object.assign({}, i, {
          [options.children__]: [],
          __childrenLength: Array.isArray(i[options.children__])
            ? i[options.children__].length
            : 0,
        });
        return new_node;
      });
      // 
      const assembly_data = new_nodes.reduce((pre, item, idx, arr) => {
        const find_parent = arr.find((i) => i[options.id__] == item[options.pid__]);
        if (!find_parent) return pre.concat(item);
        Array.isArray(find_parent[options.children__])
          ? find_parent[options.children__].push(item)
          : (find_parent[options.children__] = [item]);
        return pre;
      }, []);
      // 
      assembly_data.forEach((i) => {
        // 
        const inThere = valInDeep(
          to_data,
          i[options.id__],
          options.id__,
          options.children__
        );
        if (inThere.length) {
          // 
          this.$refs[from_ref].remove(i);
          return;
        }
        // 
        const children_num = Array.isArray(i[options.children__])
          ? i[options.children__].length
          : 0;
        const all_children_transfer = children_num === i.__childrenLength;
        delete i.__childrenLength;
        // ;
        // ,;
        this.findParentInTarget(i, options, from_data, to_data, from_ref, to_ref, isAdd);
        // 
        if (all_children_transfer) {
          this.$refs[from_ref].remove(i);
        }
      });
    },
    /**
     * @name 
     * @param {Object} item 
     * @param {options} options { children__, pid__, id__, root__ }
     * @param {Array} from_data 
     * @param {Array} to_data 
     * @param {String} from_ref dom
     * @param {String} to_ref dom
     * @param {Boolean} isAdd 
     */
    findParentInTarget(item, options, from_data, to_data, from_ref, to_ref, isAdd) {
      const parentInThere = valInDeep(
        to_data,
        item[options.pid__],
        options.id__,
        options.children__
      );
      // 
      if (parentInThere.length) {
        this.$refs[to_ref].append(item, item[options.pid__]);
        // 
        if (!isAdd) return;
        const [parent_node] = valInDeep(
          from_data,
          item[options.pid__],
          options.id__,
          options.children__
        );
        this.$nextTick(() => {
          if (parent_node[options.children__].length === 0) {
            this.$refs[from_ref].remove(parent_node);
          }
        });
        return;
      }
      // 
      // 
      if (item[options.pid__] === options.root__) {
        this.$refs[to_ref].append(item);
        return;
      }
      // pid
      const [parent_node] = valInDeep(
        from_data,
        item[options.pid__],
        options.id__,
        options.children__
      );
      const _parent_node = Object.assign({}, parent_node, {
        [options.children__]: [item],
      });
      // 
      if (isAdd) {
        this.$refs[from_ref].remove(item);
      }
      this.findParentInTarget(
        _parent_node,
        options,
        from_data,
        to_data,
        from_ref,
        to_ref,
        isAdd
      );
    },
    // 
    leftloadNode(node, resolve) {
      this.lazyFn && this.lazyFn(node, resolve, "left");
    },
    // 
    rightloadNode(node, resolve) {
      this.lazyFn && this.lazyFn(node, resolve, "right");
    },
    //  - 
    fromTreeChecked(nodeObj, treeObj) {
      this.from_check_keys = treeObj.checkedNodes;
      this.$nextTick(() => {
        this.$emit("left-check-change", nodeObj, treeObj, this.from_check_all);
      });
    },
    //  - 
    toTreeChecked(nodeObj, treeObj) {
      this.to_check_keys = treeObj.checkedNodes;
      this.$nextTick(() => {
        this.$emit("right-check-change", nodeObj, treeObj, this.to_check_all);
      });
    },
    //  checkbox
    fromAllBoxChange(val) {
      if (this.self_from_data.length == 0) {
        return;
      }
      if (val) {
        this.from_check_keys = this.self_from_data;
        this.$refs["from-tree"].setCheckedNodes(this.self_from_data);
      } else {
        this.$refs["from-tree"].setCheckedNodes([]);
        this.from_check_keys = [];
      }
      this.$emit("left-check-change", null, null, this.from_check_all);
    },
    //  checkbox
    toAllBoxChange(val) {
      if (this.self_to_data.length == 0) {
        return;
      }
      if (val) {
        this.to_check_keys = this.self_to_data;
        this.$refs["to-tree"].setCheckedNodes(this.self_to_data);
      } else {
        this.$refs["to-tree"].setCheckedNodes([]);
        this.to_check_keys = [];
      }
      this.$emit("right-check-change", null, null, this.to_check_all);
    },
    //  
    filterNodeFrom(value, data) {
      if (this.filterNode) {
        return this.filterNode(value, data, "form");
      }
      if (!value) return true;
      return data[this.defaultProps.label].indexOf(value) !== -1;
    },
    // 
    filterNodeTo(value, data) {
      if (this.filterNode) {
        return this.filterNode(value, data, "to");
      }
      if (!value) return true;
      return data[this.defaultProps.label].indexOf(value) !== -1;
    },
    //  
    addressListTransfer(type) {
      // 
      let arrayCheckedNodes = this.$refs["from-tree"].getCheckedNodes(true);
      // 
      let arrayDeWeighting = [];
      switch (type) {
        case 0:
          arrayDeWeighting = arrayCheckedNodes.filter((item) => {
            if (
              !this.addressee.some(
                (ite) => ite[this.node_key] == item[this.node_key]
              )
            ) {
              return item;
            }
          });
          this.addressee = [...this.addressee, ...arrayDeWeighting];
          break;
        case 1:
          arrayDeWeighting = arrayCheckedNodes.filter((item) => {
            if (
              !this.Cc.some((ite) => ite[this.node_key] == item[this.node_key])
            ) {
              return item;
            }
          });
          this.Cc = [...this.Cc, ...arrayDeWeighting];
          break;
        case 2:
          arrayDeWeighting = arrayCheckedNodes.filter((item) => {
            if (
              !this.secret_receiver.some(
                (ite) => ite[this.node_key] == item[this.node_key]
              )
            ) {
              return item;
            }
          });
          this.secret_receiver = [...this.secret_receiver, ...arrayDeWeighting];
          break;
      }

      // 
      this.$refs["from-tree"].setCheckedKeys([]);

      // 
      this.from_check_keys = [];

      // 
      this.$emit("addBtn", this.addressee, this.Cc, this.secret_receiver);
    },
    //   
    clearList(type, id) {
      switch (type) {
        case 0:
          this.addressee =
            id == "all"
              ? []
              : this.addressee.filter((item) => item[this.node_key] != id);
          break;
        case 1:
          this.Cc =
            id == "all"
              ? []
              : this.Cc.filter((item) => item[this.node_key] != id);
          break;
        case 2:
          this.secret_receiver =
            id == "all"
              ? []
              : this.secret_receiver.filter(
                  (item) => item[this.node_key] != id
                );
          break;
      }
      // 
      this.$emit("removeBtn", this.addressee, this.Cc, this.secret_receiver);
    },
    //   
    moveUp(type) {
      if (type == "up") {
        this.move_up = true;
      } else {
        this.move_up = false;
      }
    },
    //  ------------------------------------------------------------------------------------------------------
    /**
     * @name 
     * @param {String} type left right all all
     */
    clearChecked(type = "all") {
      if (type === "left") {
        this.$refs["from-tree"].setCheckedKeys([]);
        this.from_is_indeterminate = false;
        this.from_check_all = false;
      } else if (type === "right") {
        this.$refs["to-tree"].setCheckedKeys([]);
        this.to_is_indeterminate = false;
        this.to_check_all = false;
      } else {
        this.$refs["from-tree"].setCheckedKeys([]);
        this.$refs["to-tree"].setCheckedKeys([]);
        this.from_is_indeterminate = false;
        this.from_check_all = false;
        this.to_is_indeterminate = false;
        this.to_check_all = false;
      }
    },
    /**
     * @name 
     */
    getChecked() {
      // 
      let leftKeys = this.$refs["from-tree"].getCheckedKeys();
      let leftHarfKeys = this.$refs["from-tree"].getHalfCheckedKeys();
      let leftNodes = this.$refs["from-tree"].getCheckedNodes();
      let leftHalfNodes = this.$refs["from-tree"].getHalfCheckedNodes();
      // 
      let rightKeys = this.$refs["to-tree"].getCheckedKeys();
      let rightHarfKeys = this.$refs["to-tree"].getHalfCheckedKeys();
      let rightNodes = this.$refs["to-tree"].getCheckedNodes();
      let rightHalfNodes = this.$refs["to-tree"].getHalfCheckedNodes();
      return {
        leftKeys,
        leftHarfKeys,
        leftNodes,
        leftHalfNodes,
        rightKeys,
        rightHarfKeys,
        rightNodes,
        rightHalfNodes,
      };
    },
    /**
     * @name 
     * @param {Array} leftKeys ids
     * @param {Array} rightKeys ids
     */
    setChecked(leftKeys = [], rightKeys = []) {
      this.$refs["from-tree"].setCheckedKeys(leftKeys);
      this.$refs["to-tree"].setCheckedKeys(rightKeys);
    },
  },
  computed: {
    // 
    self_from_data() {
      let from_array = [...this.from_data];
      return !this.arrayToTree
        ? from_array
        : arrayToTree(from_array, {
            id: this.node_key,
            pid: this.pid,
            children: this.defaultProps.children,
          });
    },
    // 
    self_to_data() {
      let to_array = [...this.to_data];
      return !this.arrayToTree
        ? to_array
        : arrayToTree(to_array, {
            id: this.node_key,
            pid: this.pid,
            children: this.defaultProps.children,
          });
    },
    // 
    fromTitle() {
      let [text] = this.title;
      return text;
    },
    // 
    toTitle() {
      let [, text] = this.title;
      return text;
    },
    // 2
    toTitleSecond() {
      let [, , text] = this.title;
      return text;
    },
    // 3
    toTitleThird() {
      let [, , , text] = this.title;
      return text;
    },
    // 
    fromButton() {
      if (this.button_text == undefined) {
        return;
      }

      let [text] = this.button_text;
      return text;
    },
    // 
    toButton() {
      if (this.button_text == undefined) {
        return;
      }
      let [, text] = this.button_text;
      return text;
    },
  },
  watch: {
    //  
    from_check_keys(val) {
      if (val.length > 0) {
        // 
        this.from_disabled = false;
        // 
        this.from_is_indeterminate = true;

        //  - 
        let allCheck = val.filter((item) => item[this.pid] == 0);
        if (allCheck.length == this.self_from_data.length) {
          //  
          this.from_is_indeterminate = false;
          this.from_check_all = true;
        } else {
          this.from_is_indeterminate = true;
          this.from_check_all = false;
        }
      } else {
        this.from_disabled = true;
        this.from_is_indeterminate = false;
        this.from_check_all = false;
      }
    },
    //  
    to_check_keys(val) {
      if (val.length > 0) {
        // 
        this.to_disabled = false;
        // 
        this.to_is_indeterminate = true;

        //  - 
        let allCheck = val.filter((item) => item[this.pid] == 0);
        if (allCheck.length == this.self_to_data.length) {
          //  
          this.to_is_indeterminate = false;
          this.to_check_all = true;
        } else {
          this.to_is_indeterminate = true;
          this.to_check_all = false;
        }
      } else {
        this.to_disabled = true;
        this.to_is_indeterminate = false;
        this.to_check_all = false;
      }
    },
    //  
    filterFrom(val) {
      this.$refs["from-tree"].filter(val);
    },
    //  
    filterTo(val) {
      this.$refs["to-tree"].filter(val);
    },
    //  1
    filterListFirst(newval, oldval) {
      if (oldval == "") {
        this.archiveFirst = this.addressee;
      }
      if (newval == "") {
        this.addressee = this.archiveFirst;
      }
      let reg = RegExp(newval);
      this.addressee = this.addressee.filter((item) => reg.test(item.label));
    },
    //  2
    filterListSecond(newval, oldval) {
      if (oldval == "") {
        this.archiveSecond = this.Cc;
      }
      if (newval == "") {
        this.Cc = this.archiveSecond;
      }
      let reg = RegExp(newval);
      this.Cc = this.Cc.filter((item) => reg.test(item.label));
    },
    //  3
    filterListThird(newval, oldval) {
      if (oldval == "") {
        this.archiveThird = this.secret_receiver;
      }
      if (newval == "") {
        this.secret_receiver = this.archiveThird;
      }
      let reg = RegExp(newval);
      this.secret_receiver = this.secret_receiver.filter((item) =>
        reg.test(item.label)
      );
    },
    // 
    defaultCheckedKeys: {
      handler(val) {
        this.from_check_keys = val || [];
        if (this.defaultTransfer && this.from_check_keys.length) {
          this.$nextTick(() => {
            this.addToAims(false);
          });
        }
      },
      immediate: true,
    },
    // 
    defaultExpandedKeys: {
      handler(val) {
        let _form = new Set(this.from_expanded_keys.concat(val));
        this.from_expanded_keys = [..._form];
        let _to = new Set(this.to_expanded_keys.concat(val));
        this.to_expanded_keys = [..._to];
      },
      immediate: true,
    },
    // 
    sjr: {
      handler(val) {
        this.addressee.push(...val);
      },
      immediate: true,
    },
    // 
    csr: {
      handler(val) {
        this.Cc.push(...val);
      },
      immediate: true,
    },
    // 
    msr: {
      handler(val) {
        this.secret_receiver.push(...val);
      },
      immediate: true,
    },
  },
};
</script>

<style lang="scss">
@import "./clear.css";

.wl-transfer {
  position: relative;
  overflow: hidden;
  .el-tree {
    min-width: 100%;
    display: inline-block !important;
  }
  .transfer-left {
    position: absolute;
    top: 0;
    left: 0;
  }

  .transfer-right {
    position: absolute;
    top: 0;
    right: 0;
  }

  .transfer-right-item {
    height: calc((100% - 41px) / 2);
  }

  .transfer-right-small {
    height: 41px;
  }

  .transfer-right-only {
    height: 100%;
  }

  .transfer-main {
    padding: 10px;
    height: calc(100% - 41px);
    box-sizing: border-box;
    overflow: auto;
  }

  .transfer-left,
  .transfer-right {
    border: 1px solid #ebeef5;
    width: 40%;
    height: 100%;
    box-sizing: border-box;
    border-radius: 5px;
    vertical-align: middle;
  }

  .transfer-center {
    position: absolute;
    top: 50%;
    left: 40%;
    width: 20%;
    transform: translateY(-50%);
    text-align: center;
  }

  .transfer-center-item {
    padding: 10px;
    overflow: hidden;
  }

  .address-list-center {
    height: 100%;
  }

  .address-list-center > .transfer-center-item {
    height: 50%;
    padding: 70px 10px 0;
    box-sizing: border-box;
    overflow: hidden;
  }

  .address-list-center > .address-only-item {
    height: 100%;
    position: relative;
  }

  .address-only-item > .address-first-btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .transfer-title {
    border-bottom: 1px solid #ebeef5;
    padding: 0 15px;
    height: 40px;
    line-height: 40px;
    color: #333;
    font-size: 16px;
    background-color: #f5f7fa;
  }

  .transfer-title .el-checkbox {
    margin-right: 10px;
  }

  .filter-tree {
    margin-bottom: 10px;
  }

  .address-list-ul {
    padding-bottom: 20px;
  }

  .address-list-li {
    position: relative;
    padding: 4px 24px 4px 4px;
    border-radius: 3px;
    overflow: hidden; /**/
    white-space: nowrap; /**/
    text-overflow: ellipsis; /*...*/
  }

  .address-list-li:hover {
    background-color: #f5f7fa;
  }

  .address-list-li:hover .address-list-del {
    display: block;
  }

  .address-list-del {
    display: none;
    position: absolute;
    top: 50%;
    right: 2px;
    margin-top: -10px;
    width: 20px;
    height: 20px;
    line-height: 20px;
    border-radius: 50%;
    text-align: center;
    background-color: #fef0f0;
    color: #f56c6c;
    cursor: pointer;
  }

  .u-clear {
    float: right;
    color: #67c23a;
    font-size: 14px;
    cursor: pointer;
  }

  .move_up_img {
    float: right;
    margin-top: 10px;
    width: 20px;
    height: 20px;
    cursor: pointer;
  }

  .move_down_img {
    transform: rotate(180deg);
  }
}
</style>
